* {
  margin: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Navbar */
.Navbar {
  display: flex;
  justify-content: space-between;
  background: none;
}
.my-face {
  margin: 15px;
}
.my-face img {
  border-radius: 50px;
  height: 60px;
  width: 60px;
  filter: grayscale(40%);
}
.nav-links {
  display: flex;
  justify-content: space-around;
  margin-right: 25px;
}
.nav-links div {
  padding: 25px;
}
.nav-links div a:hover {
  color: gray;
}
.nav-links a {
  color: black;
  font-size: 20px;
  font-weight: 600;
  text-decoration: none;
}

/* Home */
.Home{
  background-color: white;
  background: url(images/fog.jpg) no-repeat;
  background-size: cover;
  height: 660px;
}
.mainHome {
  display: grid;
  grid-template-columns: 70% 30%;
}
.intro {
  margin-top: 90px;
  line-height: 85px;
  margin-left: 12%;
  float: left;
}
.hi {
  /*color: lightslategrey;*/
  color: #595959;
  font-size: 60px;
  font-family: 'Baloo Tamma 2', cursive;
}
.shreyas {
  line-height: 115px;
  font-size: 7em;
  color: black;
  font-family: 'Srisakdi', cursive;
  animation: shreyas 4s infinite;
  animation-delay: 0.5s;
  animation-fill-mode: forwards;
}
@keyframes shreyas {
  0% {color: black;}
  50% {color: gray;}
  100% {color: black;}
}
@keyframes bounce-img {
  0% {margin-top: 0%;}
  50% {margin-top: 100%; transform: rotate(180deg);}
  100% {margin-top: 0%; transform: rotate(360deg);}
}
.my-face-bounce img {
  border-radius: 50px;
  height: 75px;
  width: 75px;
  float: left;
  animation-name: bounce-img;
  animation-delay: 0.5s;
  animation-duration: 1.5s;
  margin-top: 0%;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
  filter: grayscale(60%);
}
.my-face-bounce{
  text-align: center;
  max-height: 100%;
  border-bottom: 5px solid lightgray;
}

/* About */
.about-bg {
  background: url(./images/clown2.jpg);
  background-size: cover;
}
.about {
  display: grid;
  grid-template-columns: 60% 40%;
  margin: 0px 4%;
}
.about-me {
  text-align: center;
  margin: 0px;
}
.about-me p {
  margin: 4%;
  padding: 4%;
  font-size: 30px;
  line-height: 150%;
  font-family: 'Dancing Script', cursive;
  color: white;
}
.skills {
  background-color: lightcyan;
  border-radius: 15px;
  border: 1px solid cyan;
  margin: 0px 100px;
  margin-top: 4%;
  padding: 1%;
  opacity: 0.8;
  text-align: center;
}
.skills h2 {
  font-size: 25px;
  font-weight: normal;
  margin: 15px;
}
.skills-ul {
  list-style-type: none;
  display: grid;
  line-height: 150%;
  font-size: 18px;
}
.my-picture img {
  margin: 15px 10px;
  border-radius: 10px;
  filter: brightness(80%)
}

/* Projects */
.projects-div {
  margin: 0px;
  background: url(images/sky.svg);
  background-size: cover;
  padding-bottom: 1%;
}
.projects-div-all {
  margin: 0px;
  background: url(images/sky.svg);
  background-size: cover;
}
.projects-div h3 {
  color: white;
  font-size: 50px;
  font-weight: lighter;
  text-align: center;
  padding: 2%;
}
.projects-div-all h1 {
  color: white;
  font-size: 50px;
  font-weight: lighter;
  text-align: center;
  padding: 1%;
}
.projects {
  display: grid;
  grid-template-columns: 25% 25% 25% 25%;
  overflow: hidden;
  margin: 2% 3%;
}
.card {
  background-color: black;
  color: white;
  margin: 0%;
  width: 95%;
  border-radius: 7px;
  padding-bottom: 2px;
}
.all-projects-card {
  margin-bottom: 10%;
}
.card-img img {
  border-radius: 7px;
  border-bottom-right-radius: 0px;
  border-bottom-left-radius: 0px;
}
.card-content {
  padding: 10px;
  font-weight: lighter;
  text-align: center;
  margin-bottom: 7px;
  color: lightgray;
}
.card-title {
  text-align: center;
  font-size: 22px;
  margin: 5px;
  color: white;
  font-weight: normal;
}
.tech {
  margin-top: 15px;
}
.card-link {
  padding: 3% 38%;
  border-radius: 5px;
  background-color: skyblue;
  color: black;
  text-decoration: none;
  font-weight: normal;
  transition: 0.5s;
}
.card-link-age-calc {
  padding: 3% 35%;
  border-radius: 5px;
  background-color: skyblue;
  color: black;
  text-decoration: none;
  font-weight: normal;
  transition: 0.5s;
}
.card-link:hover, .card-link-age-calc:hover {
  background-color: lightyellow;
}
.go-to-all-projects {
  text-align: center;
  font-size: 50px;
  padding-top: 50%;
  cursor: pointer;
  transition: 0.75s;
  text-decoration: none;
}
.go-to-all-projects:hover {
  background-color: white;
  color: black;
}
.go-to-all-projects img {
  background-color: black;
}
.go-to-all-projects-mob {
  display: none;
}

/* Contact */
.contact {
  border-radius: 10px;
  display: grid;
  grid-template-columns: 60% 40%;
  background-color: black;
  color: white;
  padding: 9%;
  margin: 0 5%;
  background-image: linear-gradient(to bottom right, blue, lightblue);
}
.contact-div {
  padding-bottom: 2%;
  background-image: linear-gradient(to right, yellow, red);
}
.contact-div h4 {
  font-size: 50px;
  text-align: center;
  padding: 2%;
  font-weight: normal;
}
.contact-info {
  font-size: 30px;
  line-height: 175%;
}
.social img {
  width: 75px;
  height: 75px;
  margin: 15px;
  transition: 0.25s;
}
.social img:hover {
  opacity: 0.5;
}
.social {
  display: flex;
  padding: 50px;
}
#invert {
  filter: invert(90%);
}

/* Footer */
.footer {
  background-color: black;
  color: white;
  text-align: center;
  padding: 1.5%;
}
.footer span a {
  color: lightblue;
  text-decoration: none;
}
.footer span a:hover {
  color: skyblue;
  text-decoration: none;
}


